import { useMemo } from "react"
import { Avatar, Box, Typography } from "@rp/react/web/mui/core"
import { Business, Person, Public } from "@rp/react/web/mui/icons"
import { EllipsisTooltip } from "../EllipsisTooltip"
import { RadioMenu } from "../RadioMenu"

type ShareOptions = "Private" | "Organization" | "Public"

const getSharedOptionsData = (
  dataType: string,
  orgName: string,
): Record<
  ShareOptions,
  {
    icon: React.ReactNode
    title: string
    description: string
  }
> => ({
  Private: {
    title: "Private",
    description: `Only you have access to the ${dataType}`,
    icon: <Person />,
  },
  Organization: {
    title: orgName,
    description: "Anyone in your organization with the link can view",
    icon: <Business />,
  },
  Public: {
    title: "Public",
    description: `Anybody with the link can view your ${dataType}`,
    icon: <Public />,
  },
})

type Props = {
  sharedStatus: "Private" | "Organization" | "Public"
  setSharedStatus: (sharedStatus: "Private" | "Organization" | "Public") => void
  orgName: string
  isUserOwner: boolean
  dataType: string
  enabledOptions: Array<ShareOptions>
}

const ShareOptionSelector = ({
  sharedStatus,
  setSharedStatus,
  orgName,
  isUserOwner,
  dataType,
  enabledOptions,
}: Props) => {
  const sharedOptionsData = useMemo(
    () => getSharedOptionsData(dataType, orgName),
    [dataType, orgName],
  )
  return (
    <>
      <Typography fontSize={18} fontWeight="bold" my="12px">
        General access
      </Typography>
      <Box display="flex" gap="10px">
        <Avatar
          sx={{
            backgroundColor: "overlay.surface",
            color: "text.secondary",
            fontSize: "body1.fontSize",
          }}
        >
          {sharedOptionsData[sharedStatus].icon}
        </Avatar>
        <Box width={"calc(100% - 50px)"}>
          <Box sx={{ display: "flex", alignItems: "center", gap: 0.5 }}>
            {isUserOwner ? (
              <RadioMenu
                value={sharedStatus}
                items={enabledOptions.map(sharedOption => ({
                  label: sharedOptionsData[sharedOption].title,
                  value: sharedOption,
                }))}
                onChange={value => {
                  setSharedStatus(value)
                }}
              />
            ) : null}
            {!isUserOwner ? (
              <EllipsisTooltip tooltipText={orgName ?? ""}>{orgName}</EllipsisTooltip>
            ) : null}
          </Box>
          <Typography color="grey.300">{sharedOptionsData[sharedStatus].description}</Typography>
        </Box>
      </Box>
    </>
  )
}
export { ShareOptionSelector }
